
.listnote{
	width: 100%;
	box-sizing: border-box;
	padding: 12px 16px 0px 16px;
	background: #FFF;
}
.listnote .list{
	 overflow: hidden;
}
.listnote .list li{
	position: relative;
	height: 48px;
}
.listnote .raduis{
	width: 8px;
	height: 8px;
	border-radius:50%;
	border: 2px solid #b3d2f8;
	position: absolute;
	top: 8px;
	left: 2px;
}
.listnote .line{
	width: 2px;
	height:38px;
	background:#b3d2f8;
	position: absolute;
	top: 20px;
	left: 7px;
}
.listnote .raduis1{
	width: 12px;
	height: 12px;
	left: 0px;
}
.listnote .line1{
	height:36px;
	top: 22px;
}
.listnote .trainname{
	font-size: 16px;
	color: #666;
	margin-left: 26px;
	padding-top: 2px;
}
.listnote .trainname1{
	font-size: 20px;
	color: #333;
	margin-left: 26px;
	padding-top: 0px;
}
.listnote .traintime{
	font-size: 12px;
	color: #999;
	position: absolute;
	right: 0;
	top: 8px;
}
.listnote .list li:last-child>.line{
	display: none;
}



